@import "styles/reset.css";
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

@font-face {
    font-family: 'Pulsar';
    src: local('Pulsar'),
    url('./fonts/pulsar/PulsarOriginalRegular.woff2') format('woff2');
}
@font-face {
    font-family: 'Aire-Exterior';
    src: local('Aire-Exterior'),
    url("./fonts/aireExterior/AireExterior.ttf") format('truetype');
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #141C24;
    color: #F1F1F1;
    font-family: 'Roboto', sans-serif;
    overflow-x: hidden;
    font-size: 21px;
}

section {
    max-width: 1440px;
    margin: 0 auto;
}
h2 {
    font-family: 'Aire-Exterior',sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 39px;
    line-height: 39px;
    color: #FFFFFF;
}
.flex {
    display: flex;
    align-items: center;
}
.gap30 {
    gap: 30px;
}


