.my_title span {
    display: block;
    width: max-content;
    position: relative;
    font-family: 'Aire-Exterior', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #57D6FF;
}

.my_title span:before {
    content: "";
    position: absolute;
    width: 50px;
    height: 1px;
    background-color: #57D6FF;
    top: 50%;
    left: -90px;
}

@media (max-width: 776px) {
    .my_title span {
        margin: 0 auto;
    }
}